/* Temporary */
.degree-filters {
  flex: 1 1 320px;
}
@media (min-width: 800px) {
.degree-filters {
    max-width: 320px;
}
}
.degree-list {
  flex: 1 1 calc(100% - 360px);
}
main.content {
  width: 1440px;
  display: flex;
  justify-content: space-between;
  margin: 4em auto;
  max-width: 100%;
  padding: 1.25em;
}
.filter-clear {
  display: flex;
  align-items: center;
  cursor: pointer;
}



