
.search-filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row;
  align-content: start;
}
.search-filter-input {
  height: 40px;
  width: 100%;
  background: #eee;
  padding: .25em .5em;
}
.search-filter .search-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #cc1f1b;
  width: 40px;
  height: 40px;
}



